"use strict";

import Rx from "rx-lite";

import {w3cwebsocket as WebSocket} from "websocket";

const OPEN = 1;

export let identity = x => x;

export let random = (min, max) =>
	min + Math.floor(Math.random() * (max - min + 1));

export let fromWebSocket = (
	url,
	openHandler = identity,
	messageHandler = identity,
	errorHandler = identity,
	closeHandler = identity
) => {
	let socket;
	let queue = [];
	let isOpen = () => socket && socket.readyState === OPEN;

	function socketClose(code, reason) {
		if (socket) {
			if (!code) {
				socket.close();
			} else {
				socket.close(code, reason);
			}
		}
	}

	let observable = Rx.Observable.create(obs => {
		socket = new WebSocket(url);

		function onOpen() {
			while (queue.length) {
				queue.shift()();
			}
			openHandler();
			socket.removeEventListener("open", onOpen, false);
		}
		function onClose(e) {
			// treat 1005 code as normal closure for now
			// FIXME when RTM team employ the normal closure
			if (e.code !== 1000 && e.code !== 1005) {
				errorHandler(e);
				obs.onError(e);
			} else {
				closeHandler();
				obs.onCompleted();
			}
		}

		socket.addEventListener("open", onOpen, false);
		socket.addEventListener("message", messageHandler, false);
		socket.addEventListener("close", onClose, false);

		return () => {
			socket.removeEventListener("message", messageHandler, false);
			socket.removeEventListener("close", onClose, false);
			queue = [];
		};
	});

	let observer = Rx.Observer.create(
		data => {
			if (isOpen()) {
				socket.send(data);
			} else {
				queue.push(() => isOpen() && socket.send(data));
			}
		},
		e => socketClose(e.code, e.reason || ""),
		() => socketClose(1000, "")
	);

	return Rx.Subject.create(observer, observable);
};
